.pageContent {
  box-sizing: border-box;
  width: 100%; }

.pageContentLeft {
  padding: 0;
  width: 100%;
  position: absolute;
  top: calc(350px + 150px + 60px + 20px); }

.pageContentRight {
  width: 100%;
  height: 50px;
  top: auto;
  display: block;
  margin-top: 10px; }

.mapStyles {
  width: calc(100% - 20px);
  height: 400px;
  display: block;
  margin: 0 10px 10px 10px; }

.parkContainer {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: calc(100% - 20px);
  margin: 0 10px 10px 10px;
  background: #fff; }
  .parkContainer .Box {
    width: 100%; }
  .parkContainer img {
    width: 100%;
    height: 120px;
    object-fit: cover; }
  .parkContainer h2 {
    padding-left: 5px;
    margin-top: 0;
    font-family: "Quicksand", sans-serif; }
  .parkContainer p {
    padding-left: 5px;
    font-family: "Open Sans", sans-serif;
